<template>
    <div class="container">
    <div class="card card-primary">
      <div class="card-header">
        <h3 class="card-title">На головній сторінці зверху</h3>
      </div>
      <form @submit.prevent="saveBaner">
        <div class="card-body">
          <div class="file-input">
              <input type="file" ref="fileInput2" class="input-baner" @change="handleFileChange" multiple>
              <button @click="triggerFileInput" class="btn btn-baner btn-default" v-if="banerTop.length === 0">Добавити</button>
              <div class="delete-img" v-if="banerTop.length >= 1">
                <div class="image-delete" v-for="(banerTopImg, i) in banerTop" :key="i">
                  <img :src="banerTopImg" class="imgInput" v-if="banerTopImg">
                  <span class="deleteSpan" v-if="banerTopImg" @click="deleteBannerTop(i)">x</span>
                </div>
                <button @click="triggerFileInput" class="btn btn-block btn-default"  v-if="banerTop.length >= 1">Добавити</button>
              </div>
          </div>
          <!-- <div class="file-input" v-if="doneTop === true">
              <input type="file" ref="fileInput22" class="input-baner" @change="handleFileChange" multiple>
              <div class="delete-img">
                <div class="image-delete" v-for="(banerTopImg, i) in banerTop" :key="i">
                  <img :src="banerTopImg" v-if="banerTopImg" class="imgInput">
                  <span class="deleteSpan" v-if="banerTopImg" @click="deleteBannerTop(i)">x</span>
                </div>
                <button @click="triggerFileInput" class="btn btn-block btn-default">Добавити2</button>
              </div>
          </div> -->
        </div> 
        <div v-if="showMessage" class="message">
          {{ message }}
        </div>
        <div class="card-footer">
          <button @click="saveBaner" class="btn btn-primary" type="submit">Зберегти</button>
        </div>
      </form>
    </div>
  </div>
  </template>
  <script>
  
  import { getDatabase, ref as dbRef, set, push, update, onValue, remove, get} from 'firebase/database'
  import { getStorage, ref as storRef, uploadBytes, put, getDownloadURL, deleteObject, listAll } from 'firebase/storage'
  
  export default {
  
      data(){
        return{
          banners: {},
          banerTop: [],
          fileBanerTop: [],
          message: 'Збережено!',
          doneTop: false,
        }
      },
  
      methods:{
        handleFileChange(event) {
            // this.fileBanerTop = [...event.target.files]
            // for(let image of this.fileBanerTop){
            //   const imageSrc = URL.createObjectURL(image)
            //   this.banerTop.push(imageSrc)
            // }
            const files = event.target.files;

          for (let i = 0; i < files.length; i++) {
            const file = files[i];
            const fileUrl = URL.createObjectURL(file);

            this.banerTop.push(fileUrl);
            this.fileBanerTop.push(file);
          }
        },
        triggerFileInput(){
          this.$refs.fileInput2.click();
        },
        deleteBanerTop(i){
          this.banerTop.splice(i, 1)
          this.fileBanerTop.splice(i, 1)
        },
        async deleteBannerTop(i){

          this.banerTop.splice(i, 1)
          this.fileBanerTop.splice(i, 1)

        //Storage
          const storage = getStorage();
          const fileRef = storRef(storage, `banners/bannerTop/${i}`);
          try {
            await deleteObject(fileRef);
            console.log(`Файл з індексом ${i} успішно видалено`);
          } catch (error) {
            console.error(`Помилка видалення файлу з індексом ${i}:`, error);
          }
          //BD
          const db = getDatabase();
          const pathDb = `banners/bannerTop/${i}`
          const itemRef = dbRef(db, pathDb);
  
          await remove(itemRef)
            .then(() => {
              console.log("Item successfully deleted from Realtime Database");
            })
            .catch((error) => {
              console.log("Error deleting item from Realtime Database: ", error);
            });
        },
        async saveBaner(){
          const db = getDatabase()
          const storage = getStorage()
          const path = 'banners'
          const moviesRef = dbRef(db, path)

          const snapshot = await get(moviesRef)
          const oldBannerTop = (snapshot.val() && snapshot.val().bannerTop) || []
          const newBannerTop = [...oldBannerTop]

          await Promise.all(this.fileBanerTop.map(async (image, index) => {
          const storageRef = storRef(storage, `banners/bannerTop/${index}`)
          await uploadBytes(storageRef, image)
          const url = await getDownloadURL(storageRef)
          newBannerTop.push(url)
        }))

        update(moviesRef, {
          bannerTop: newBannerTop
        })



          // this.fileBanerTop.forEach((banerTop, index) => {
          //   const storageRef = storRef(storage, `banners/bannerTop/${index}`);
          //   uploadBytes(storageRef, banerTop).then((snapshot) => {
          //     getDownloadURL(snapshot.ref).then((url) => {
          //       bannerTop[index] = url;
          //         update(moviesRef, {
          //           bannerTop
          //         });
          //     });
          //   });
          // });
        },
        async getBanners() {
          const db = getDatabase()
          const bannerRef = dbRef(db, 'banners');
          await new Promise((resolve) => {
            onValue(bannerRef, (snapshot) => {
              if (snapshot.exists()) {
                this.banners = snapshot.val();
                this.doneTop = true
                this.banerTop = this.banners.bannerTop
                //this.fileBanerTop = this.banners.banerTop
              } else {
                console.log('Баннер не знайдено');
                this.doneTop = false
              }
              resolve();
            });
          });
        },
      },
  
      async mounted() {
        await this.getBanners();
      },
    
  }
  </script>
  
  
  <style lang="scss">
  
  .container{
    width: 70%;
  }
  
  .upload-button {
  display: inline-block;
  border: 1px solid #ccc;
  padding: 6px 12px;
  cursor: pointer;
  }
  
  .upload-preview {
  display: inline-block;
  width: 100px;
  height: 100px;
  border: 1px solid #ccc;
  margin-left: 20px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  }
  label{
  display: block;
  text-align: left;
  }
  .file-input {
  position: relative;
  }
  .input-baner {
    position: absolute;
    top: 0.5rem;
    left: 0;
    opacity: 0;
    width: 30%;
    cursor: pointer;
    height: 38px;
  }
  .btn-baner {
    display: block;
    width: 30%;
    margin: 0.5rem 0;
  }
  .btn-block {
    display: block;
    width: 30%;
  }
  .imgInput{
  width: 250px;
  height: 250px;
  display: block;
  }
  .image-delete_btn{
  display: flex;
  gap: 40px;
  align-items: end;
  margin-bottom: 0.5rem;
  }
  .delete-img{
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: end;
  margin-bottom: 0.5rem;
  }
  .deleteSpan{
  position: absolute;
  top: -8px;
  right: -8px;
  border: 1px solid black;
  background: white;
  border-radius: 50%;
  color: black;
  width: 26px;
  cursor: pointer;
  }
  .image-delete{
  position: relative;
  }
  .form-main{
  display: flex;
  gap: 20px;
  }
  .form-check-input {
  display: block;
  }
  .form-group{
  
  h5{
    text-align: start;
  }
  }
  .card-footer{
  display: flex;
  gap: 30px;
  justify-content: center;
  }
    
  </style>
  